  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 992px) {
  nav .navbar-logo {
    margin: 0 80px;
  }
}
nav .navbar-logo img {
  height: 60px;
  width: auto;
}

.navbar-left,
.navbar-right {
  flex: 1 1 0;
}

.navbar-logo {
  flex: 0 0 auto;
}

.dropdown-toggle::after {
  display: none;
}

.nav-item.dropdown .dropdown-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-item.dropdown .dropdown-wrapper .nav-link {
  padding-right: 5px;
}
@media (min-width: 992px) {
  .nav-item.dropdown:hover .dropdown-menu {
    background-color: #231f20;
    border-radius: 0;
    border: none;
    padding: 15px 0;
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    display: block;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: all 0.25s ease;
    pointer-events: none;
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    pointer-events: auto;
  }
  .nav-item.dropdown:hover .dropdown-menu .dropdown-item {
    color: #ffffff;
    padding: 5px 20px;
  }
  .nav-item.dropdown:hover .dropdown-menu .dropdown-item:hover {
    color: #ff9500;
    background-color: #231f20;
  }
}
.nav-item.dropdown .dropdown-menu.show {
  background: #231f20;
  border: none;
  width: fit-content;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: auto;
}
.nav-item.dropdown .dropdown-menu.show .dropdown-item {
  color: #ffffff;
}
@media (max-width: 991px) {
  .nav-item.dropdown .dropdown-menu.show .dropdown-item:hover {
    background-color: #231f20;
    color: #ff9500;
  }
}
@media (min-width: 992px) {
  .nav-item .navbar-expand-lg .navbar-nav .dropdown-menu {
    top: 45px;
  }
}

.navbar-toggler {
  border: none;
}

.navbar-toggler-icon {
  filter: invert(1) brightness(2);
}

@media (max-width: 991px) {
  .dropdown-menu {
    display: block !important;
    padding: 0;
    background: #231f20;
    overflow: hidden;
    max-height: 0;
    opacity: 0;
    transform: translateY(-10px);
    visibility: hidden;
    transition: max-height 0.35s cubic-bezier(0.25, 1, 0.3, 1), opacity 0.35s cubic-bezier(0.25, 1, 0.3, 1), transform 0.35s cubic-bezier(0.25, 1, 0.3, 1);
    will-change: max-height, opacity, transform;
  }
  .dropdown-menu.show {
    max-height: 999px;
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }
  .dropdown-item {
    padding: 10px 15px;
  }
}
.mobile-burger {
  position: absolute;
  right: 20px;
}

@media (max-width: 991px) {
  .navbar-collapse {
    display: block !important;
    overflow: hidden;
    transition: max-height 0.45s cubic-bezier(0.25, 1, 0.3, 1), opacity 0.45s cubic-bezier(0.25, 1, 0.3, 1), transform 0.45s cubic-bezier(0.25, 1, 0.3, 1);
    will-change: max-height, opacity, transform;
  }
  /* CLOSED */
  .navbar-collapse.collapse {
    max-height: 0 !important;
    opacity: 0;
    transform: translateY(-10px);
    visibility: hidden;
  }
  /* OPENING */
  .navbar-collapse.collapsing {
    max-height: 1000px !important;
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }
  /* OPEN */
  .navbar-collapse.collapse.show {
    max-height: 1000px !important;
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }
  /* CLOSING */
  .navbar-collapse.collapsing:not(.show) {
    max-height: 0 !important;
    opacity: 0;
    transform: translateY(-10px);
    visibility: hidden;
  }
}
